import { fns } from './patch'

let golbal = {}
let oldTree
let newTree

export function useState(state) {
  if (Object.keys(golbal).length > 0) {
    state = {
      ...state,
      ...golbal
    }
  }

  let caller = call() // counter

  return proxy(state)
}

function proxy(state) {
  let newState = new Proxy(state, {
    get(obj, key) {},
    set(obj, key, val) {
      return true
    }
  })

  return newState
}

function call() {
  try {
    throw new Error()
  } catch (e) {
    try {
      return e.stack.match(/Object.(\S*)/)[1]
    } catch (e) {
      return ''
    }
  }
}
